<template>
  <div>
    <div
      v-for="(item, index) in chartData"
      :key="index + ' only'"
      style="width: 33%; display: inline-block"
    >
      <div v-if="flag" :ref="'PieChart' + index" class="chart chartClass"></div>
    </div>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

import * as echarts from "echarts";
export default {
  props: {
    work_type: {
      type: String,
      default: "",
    },
    chartData: {
      type: Array,
      default: () => [],
    },
  },
  watch: {
    chartData(newVal, oldVal) {
      this.chartInit();
    },
  },

  data() {
    return {
      flag: true,
      myChart: null,
      noneFlag: false,
      titleTotal: 7,
      pieDivHeight: "",
      optionAll: {},
      seriesDataAll: [],
      chartList: [],
    };
  },
  methods: {
    //   获取数据  图表初始化
    chartInit() {
      this.$nextTick(() => {
        this.chartData.forEach((item, index) => {

          this.myChart = echarts.init(this.$refs["PieChart" + index][0]);
          this.optionAll = {
            title: {
              text: item.work_id + "_" + item.work_name,
              top: "5%",
              right: "center",
              textStyle: {
                // color: "#fff",
                fontSize: "16",
              },
            },
            color: ["#5470c6", "#FAC858"],
            tooltip: {
              trigger: "item",
              formatter: (params) => {
                return (
                  params.name +
                  ":  " +
                  params.value +
                  " " +
                  ((params.value / this.titleTotal) * 100).toFixed(2) +
                  "%"
                );
              },
            },
            legend: {
              type: "scroll",
              orient: "vertical",
              top: "center",
              right: "3%",
              textStyle: {
                padding: 5,
              },
            },
            series: [
              {
                type: "pie",
                radius: ["35%", "55%"],
                center: ["45%", "60%"],
                avoidLabelOverlap: false,
                label: {
                  show: false,
                  position: "center",
                },

                emphasis: {
                  label: {
                    show: true,
                  },
                },
                label: {
                  formatter: " {b|{b}：}{c}  {per|{d}%}  ",
                  borderRadius: 4,
                  rich: {
                    per: {
                      borderRadius: 4,
                    },
                  },
                },
                labelLine: {},
                data: [
                  {
                    name: "合格",
                    value: item.data.pass_num,
                    total: item.data.pass_num + item.data.fail_num,
                  },
                  {
                    name: "不合格",
                    value: item.data.fail_num,
                    total: item.data.pass_num + item.data.fail_num,
                  },
                ],
              },
            ],
          };

          this.myChart.setOption(this.optionAll);
          window.addEventListener("resize", () => {
            this.myChart.resize();
          });
        });
      });
    },
  },
  mounted() {
    //   获取数据  图表初始化
    this.chartInit();

    console.log(" mounted this.work_type ", this.work_type);
    console.log(" mounted this.chartData ", this.chartData);
  },
};
</script>

<style lang="scss" scoped>
.chartClass {
  width: 100%;
  height: 250px;
  display: inline-block;
}
</style>