<template>
  <div>
    <div v-for="(item, index) in chartData" :key="index + ' only'">
      <div v-if="flag" :ref="'BarChart' + index" class="chart chartClass"></div>
    </div>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

import * as echarts from "echarts";
export default {
  props: {
    work_type: {
      type: String,
      default: "",
    },
    chartData: {
      type: Array,
      default: () => [],
    },
  },

  watch: {
    chartData(newVal, oldVal) {
      this.chartInit();
    },
  },
  data() {
    return {
      flag: true,
      myChart: null,
      noneFlag: false,
      titleTotal: 7,
      pieDivHeight: "",
      optionAll: {},
      seriesDataAll: [],
      chartList: [],
    };
  },
  methods: {
    //   获取数据
    // 图表初始化
    chartInit() {
      this.$nextTick(() => {
        this.chartData.forEach((item, index) => {
          this.myChart = echarts.init(this.$refs["BarChart" + index][0]);
          this.optionAll = {
            title: {
              text: item.work_id + "_" + item.work_name,
              top: "5%",
              left: "3%",
              textStyle: {
                // color: "#fff",
                fontSize: "16",
              },
            },
            tooltip: {
              trigger: "item",
            },
            xAxis: {
              type: "category",
              data: item.data.xAxis_data,
            },
            yAxis: {
              type: "value",
            },
            grid: [
              {
                top: "30%",
                bottom: "5%",
                left: "5%",
                right: "5%",
                containLabel: true,
              },
            ],
            series: [
              {
                name: "包装数量",
                data: item.data.series_data,
                type: "bar",
                barWidth: 30,
                label: {
                  show: true,
                  position: "top",
                },
              },
            ],
          };

          this.myChart.setOption(this.optionAll);
          window.addEventListener("resize", () => {
            this.myChart.resize();
          });
        });
      });
    },
  },
  mounted() {
    // 图表初始化
    this.chartInit();
  },
};
</script>

<style lang="scss" scoped>
.chartClass {
  width: 100%;
  height: 300px;
}
</style>