<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="时间">
          <el-date-picker
            v-model="listQuery.daterangeArr"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd HH:mm:ss"
            format="yyyy-MM-dd HH:mm:ss"
            @change="daterange_change"
          >
          </el-date-picker>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetTable()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 加工工站 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>加工工站</span>
      </div>
      <div class="">
        <MissStationChart
          work_type="加工"
          :chartData="MissStationData"
        ></MissStationChart>
      </div>
    </el-card>

    <!-- 测试工站 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>测试工站</span>
      </div>
      <div class="">
        <MissStationIPQCChart
          work_type="测试"
          :chartData="MissStationIPQCData"
        ></MissStationIPQCChart>
      </div>
    </el-card>

    <!-- 维修工站 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>维修工站</span>
      </div>
      <div class="">
        <MissStationMChart
          work_type="维修"
          :chartData="MissStationMData"
        ></MissStationMChart>
      </div>
    </el-card>

    <!-- 包装工站 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>包装工站</span>
      </div>
      <div class="">
        <MissStationPackChart
          work_type="包装"
          :chartData="MissStationPackData"
        ></MissStationPackChart>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

import MissStationChart from "@/components/dashBoard/MissStationChart";
import MissStationMChart from "@/components/dashBoard/MissStationMChart";
import MissStationIPQCChart from "@/components/dashBoard/MissStationIPQCChart";
import MissStationPackChart from "@/components/dashBoard/MissStationPackChart";
import { formatDate } from "@/assets/libs/unit";

export default {
  components: {
    MissStationChart,
    MissStationMChart,
    MissStationIPQCChart,
    MissStationPackChart,
  },
  data() {
    return {
      intervalout: "",
      addNum: 0,
      MissStationData: [],
      MissStationMData: [],
      MissStationIPQCData: [],
      MissStationPackData: [],
      listQuery: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],
        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
      },
      listQueryBase: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],

        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
      },
    };
  },
  methods: {
    // 查询
    daterange_change() {
      if (this.listQuery.daterangeArr.length > 0) {
        this.listQuery.start_time = this.listQuery.daterangeArr[0];
        this.listQuery.end_time = this.listQuery.daterangeArr[1];
        console.log(this.listQuery);
      }
    },
    searchBtn() {
      console.log(this.listQuery.daterangeArr);
      this.getLogisticAnalyseWorkStationInfo(this.listQuery);
    },
    // 重置
    resetTable() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticAnalyseWorkStationInfo(this.listQuery);
    },

    getLogisticAnalyseWorkStationInfo(params) {
      baseManagement.getLogisticAnalyseWorkStationInfo(params).then((res) => {
        console.log("看板信息", res);
        if (res.code == 0) {
          res.data.forEach((item, index) => {
            switch (item.work_type) {
              case "加工":
                console.log(242, item);
                this.MissStationData = item.data;
                break;
              case "测试":
                this.MissStationIPQCData = item.data;
                break;
              case "维修":
                this.MissStationMData = item.data;
                break;
              case "包装":
                this.MissStationPackData = item.data;
                break;
              default:
                console.log("default", item);
                break;
            }
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    dataSetInterval() {
      if (this.intervalout) {
        clearInterval(this.intervalout);
      }
      this.intervalout = setInterval(() => {
        this.getLogisticAnalyseWorkStationInfo(this.listQuery);
      }, 1000 * 30);
    },
  },
  mounted() {
    this.getLogisticAnalyseWorkStationInfo(this.listQuery);
    this.dataSetInterval();
  },
  beforeDestroy() {
    if (this.intervalout) {
      console.log(123);
      clearInterval(this.intervalout); //关闭
    }
  },
};
</script>

<style lang="scss" scoped>
</style>